const api = 'http://hugo.info/weekendsale'
const headers = {
    'Accept': 'application/json',
}

export const create = (body) =>
    fetch(`${api}/create`, {
        method: 'POST',
        headers: {
            ...headers,
            'Content-Type': 'application/json'
        },
        body: JSON.stringify(body)
    }).then(res => res.json())


