import React, { Component } from 'react'
import escape from 'escape-string-regexp'
import { Panel, FormGroup, FormControl, ControlLabel } from 'react-bootstrap'
import PropTypes from 'prop-types'
import Serialize from 'form-serialize'
import {create} from './utils/api'

class MetaData extends Component{
    constructor(props){
        super(props)
        this.state = {
            value : this.props.data_value
        }
        this.changeHandler = this.changeHandler.bind(this)
    }

    changeHandler(e){
        this.setState({
            value:e.target.value
        })
    }
    render(){
        const {data_key,data_value,data_id} = this.props
        const clickable = ['sale_base','sale_special'];
        let value = data_value
        if (data_key==='lock_status'){
            value = data_key===1?'Locked':'Unlocked'
        }
        if (clickable.includes(data_key)){
            return <td key={data_key}>
                <input type='text' name={`${data_key}[${data_id}]`} value={this.state.value} onChange={this.changeHandler} />
            </td>
        }else{
            return <td key={data_key}>{value}</td>
        }
    }
}

const Product = ({product,id})=>{
    return (<tr>
        {Object.keys(product).map((key)=>(<MetaData key={key} data_value={product[key]} data_key={key} data_id={id}/>))}
    </tr>)
}


export default class ListProducts extends Component{
    static propTypes = {
        products: PropTypes.object,

    }
    constructor(props){
        super(props)
        this.state = {
            products: {},
            query: ''
        }
        this.queryHandler = this.queryHandler.bind(this)
    }
    componentDidMount(){
        this.setState({
            products: this.props.products
        })

    }

    queryHandler(event){
        this.setState({
            query: event.target.value.trim()
        })
    }

    FormHandler(e){
        e.preventDefault()
        const form = Serialize(e.target,{hash:true})
        create(form)
    }

    render(){
        const {products, query} = this.state
        let showProducts = {}
        if (!query){
            showProducts = products;
        }else{
            let match = new RegExp(escape(query), 'i')
            Object.keys(products).map((key)=>{
                if (match.test(products[key]['model']) || match.test(products[key]['name'])){
                    showProducts[key] = products[key];
                }
            })
        }
        return (
            <form onSubmit={this.FormHandler}>
                <Panel bsStyle="info">
                    <Panel.Heading>
                        <h3>Weekend Sale</h3>
                        <p className="text-right"><button className="btn btn-primary" type="submit">Save</button></p>
                    </Panel.Heading>
                    <Panel.Body>
                        <FormGroup>
                            <ControlLabel>Search</ControlLabel>
                            <FormControl
                                type="text"
                                value={query}
                                placeholder="Available for Name and Model search"
                                onChange={this.queryHandler}
                            />
                            <FormControl.Feedback />
                        </FormGroup>
                        {(Object.keys(showProducts).length !== Object.keys(products).length) && (<p className="text-center text-capitalize">Show {Object.keys(showProducts).length} of {Object.keys(products).length} total, show all</p>)}
                    </Panel.Body>
                    <table className="table table-bordered">
                        <thead>
                        <tr>
                            <th>Model</th>
                            <th>Name</th>
                            <th>(C)Price</th>
                            <th>(C)Special</th>
                            <th>Cost</th>
                            <th>Stock</th>
                            <th>Status</th>
                            <th>Base Price</th>
                            <th>Sale</th>
                        </tr>
                        </thead>
                        <tbody>
                        {Object.keys(showProducts).map((key)=>(<Product product={showProducts[key]} key={key} id={key}/>))}
                        </tbody>
                    </table>
                </Panel>
            </form>
        )
    }
}




